@mixin easeOut {
  transition: all 0.5s ease-out;
}

//media mixins
@mixin mediaXs {
  @media screen and (max-width: 575px) {
    @content;
  }
}

@mixin mediaSm {
  @media screen and (min-width: 576px) {
    @content;
  }
}

@mixin mediaMd {
  @media screen and (min-width: 768px) {
    @content;
  }
}

@mixin mediaLg {
  @media screen and (min-width: 992px) {
    @content;
  }
}

@mixin mediaXl {
  @media screen and (min-width: 1200px) {
    @content;
  }
}
